import React, { useEffect, useState } from "react";
import RTChart from "react-rt-chart";
import { CCol, CRow } from "@coreui/react";
import BlocData from "../bigPicture/BlocData";

class ApexChart extends React.Component {
  componentDidMount() {
    setInterval(() => this.forceUpdate(), 5000);
  }

  render() {
    const DEVICES = {
      // A VALIDER
      PanneauSolaire: 1,
      batterie: 2,
      onduleur: 3,
      mppt: 4,
      chargeur: 6,
      sortie1: 7,
      sortie2: 8,
    };
    var Voltage = Math.round(Math.random() * 100) / 100;
    var Amperage = Math.round(Math.random() * 100) / 100;
    var data = {
      date: new Date(),
      Voltage,
      Amperage,
      Puissance: Math.round(Voltage * Amperage * 100) / 100,
    };
    var flow = { duration: 500 };

    return (
      <>
        <h1 style={{ textAlign: "center" }}>Données temps réel</h1>
        <CRow style={{ padding: "20px 30px" }}>
          <CCol>
            <BlocData label="Puissance" unit="W" data={data.Puissance} />
          </CCol>

          <CCol>
            <BlocData label="Voltage" unit="V" data={data.Voltage} />
          </CCol>
          <CCol>
            <BlocData label="Amperage" unit="I" data={data.Amperage} />
          </CCol>
        </CRow>
        <h1 style={{ textAlign: "center" }}>Historique de données</h1>
        <RTChart fields={["Puissance", "Voltage", "Amperage"]} data={data} flow={flow} style={{ height: "350px" }} maxValues={10} />
      </>
    );
  }
}
export default ApexChart;
