import React from "react";
import TextField from "@material-ui/core/TextField";

const BlocData = ({ label, unit, data }) => {
  return (
    <TextField
      label={label}
      value={data + " " + unit}
      InputProps={{
        readOnly: true,
      }}
      inputProps={{
        min: 0,
        style: { textAlign: "right" },
      }}
      variant="outlined"
    />
  );
};

export default BlocData;
